import{MixedRouteSDK,Protocol,SwapRouter as SwapRouter02,Trade}from"@uniswap/router-sdk";import{TradeType}from"@uniswap/sdk-core";import{SwapRouter as UniveralRouter,UNIVERSAL_ROUTER_ADDRESS}from"@uniswap/universal-router-sdk";import{Route as V2RouteRaw}from"@uniswap/v2-sdk";import{Route as V3RouteRaw}from"@uniswap/v3-sdk";import _ from"lodash";import{CurrencyAmount,SWAP_ROUTER_02_ADDRESSES,SwapType}from"..";export function buildTrade(tokenInCurrency,tokenOutCurrency,tradeType,routeAmounts){const v3RouteAmounts=_.filter(routeAmounts,(routeAmount=>routeAmount.protocol===Protocol.V3)),v2RouteAmounts=_.filter(routeAmounts,(routeAmount=>routeAmount.protocol===Protocol.V2)),mixedRouteAmounts=_.filter(routeAmounts,(routeAmount=>routeAmount.protocol===Protocol.MIXED)),v3Routes=_.map(v3RouteAmounts,(routeAmount=>{const{route:route,amount:amount,quote:quote}=routeAmount;if(tradeType==TradeType.EXACT_INPUT){const amountCurrency=CurrencyAmount.fromFractionalAmount(tokenInCurrency,amount.numerator,amount.denominator),quoteCurrency=CurrencyAmount.fromFractionalAmount(tokenOutCurrency,quote.numerator,quote.denominator);return{routev3:new V3RouteRaw(route.pools,amountCurrency.currency,quoteCurrency.currency),inputAmount:amountCurrency,outputAmount:quoteCurrency}}{const quoteCurrency=CurrencyAmount.fromFractionalAmount(tokenInCurrency,quote.numerator,quote.denominator),amountCurrency=CurrencyAmount.fromFractionalAmount(tokenOutCurrency,amount.numerator,amount.denominator);return{routev3:new V3RouteRaw(route.pools,quoteCurrency.currency,amountCurrency.currency),inputAmount:quoteCurrency,outputAmount:amountCurrency}}})),v2Routes=_.map(v2RouteAmounts,(routeAmount=>{const{route:route,amount:amount,quote:quote}=routeAmount;if(tradeType==TradeType.EXACT_INPUT){const amountCurrency=CurrencyAmount.fromFractionalAmount(tokenInCurrency,amount.numerator,amount.denominator),quoteCurrency=CurrencyAmount.fromFractionalAmount(tokenOutCurrency,quote.numerator,quote.denominator);return{routev2:new V2RouteRaw(route.pairs,amountCurrency.currency,quoteCurrency.currency),inputAmount:amountCurrency,outputAmount:quoteCurrency}}{const quoteCurrency=CurrencyAmount.fromFractionalAmount(tokenInCurrency,quote.numerator,quote.denominator),amountCurrency=CurrencyAmount.fromFractionalAmount(tokenOutCurrency,amount.numerator,amount.denominator);return{routev2:new V2RouteRaw(route.pairs,quoteCurrency.currency,amountCurrency.currency),inputAmount:quoteCurrency,outputAmount:amountCurrency}}})),mixedRoutes=_.map(mixedRouteAmounts,(routeAmount=>{const{route:route,amount:amount,quote:quote}=routeAmount;if(tradeType!=TradeType.EXACT_INPUT)throw new Error("Mixed routes are only supported for exact input trades");const amountCurrency=CurrencyAmount.fromFractionalAmount(tokenInCurrency,amount.numerator,amount.denominator),quoteCurrency=CurrencyAmount.fromFractionalAmount(tokenOutCurrency,quote.numerator,quote.denominator);return{mixedRoute:new MixedRouteSDK(route.pools,amountCurrency.currency,quoteCurrency.currency),inputAmount:amountCurrency,outputAmount:quoteCurrency}}));return new Trade({v2Routes:v2Routes,v3Routes:v3Routes,mixedRoutes:mixedRoutes,tradeType:tradeType})}export function buildSwapMethodParameters(trade,swapConfig,chainId){if(swapConfig.type==SwapType.UNIVERSAL_ROUTER)return{...UniveralRouter.swapERC20CallParameters(trade,swapConfig),to:UNIVERSAL_ROUTER_ADDRESS(chainId)};if(swapConfig.type==SwapType.SWAP_ROUTER_02){const{recipient:recipient,slippageTolerance:slippageTolerance,deadline:deadline,inputTokenPermit:inputTokenPermit}=swapConfig;return{...SwapRouter02.swapCallParameters(trade,{recipient:recipient,slippageTolerance:slippageTolerance,deadlineOrPreviousBlockhash:deadline,inputTokenPermit:inputTokenPermit}),to:SWAP_ROUTER_02_ADDRESSES(chainId)}}throw new Error(`Unsupported swap type ${swapConfig}`)}