import{ChainId}from"@uniswap/sdk-core";import{Pair}from"@uniswap/v2-sdk";import _ from"lodash";import{WRAPPED_NATIVE_CURRENCY}from"../../util/chains";import{log}from"../../util/log";import{DAI_MAINNET,USDC_MAINNET,USDT_MAINNET,WBTC_MAINNET}from"../token-provider";const BASES_TO_CHECK_TRADES_AGAINST={[ChainId.MAINNET]:[WRAPPED_NATIVE_CURRENCY[ChainId.MAINNET],DAI_MAINNET,USDC_MAINNET,USDT_MAINNET,WBTC_MAINNET],[ChainId.GOERLI]:[WRAPPED_NATIVE_CURRENCY[ChainId.GOERLI]],[ChainId.SEPOLIA]:[WRAPPED_NATIVE_CURRENCY[ChainId.SEPOLIA]],[ChainId.OPTIMISM]:[],[ChainId.ARBITRUM_ONE]:[],[ChainId.ARBITRUM_GOERLI]:[],[ChainId.OPTIMISM_GOERLI]:[],[ChainId.POLYGON]:[],[ChainId.POLYGON_MUMBAI]:[],[ChainId.CELO]:[],[ChainId.CELO_ALFAJORES]:[],[ChainId.GNOSIS]:[],[ChainId.MOONBEAM]:[],[ChainId.BNB]:[],[ChainId.AVALANCHE]:[],[ChainId.BASE_GOERLI]:[],[ChainId.BASE]:[]};export class StaticV2SubgraphProvider{constructor(chainId){this.chainId=chainId}async getPools(tokenIn,tokenOut){log.info("In static subgraph provider for V2");const bases=BASES_TO_CHECK_TRADES_AGAINST[this.chainId],basePairs=_.flatMap(bases,(base=>bases.map((otherBase=>[base,otherBase]))));tokenIn&&tokenOut&&basePairs.push([tokenIn,tokenOut],...bases.map((base=>[tokenIn,base])),...bases.map((base=>[tokenOut,base])));const pairs=_(basePairs).filter((tokens=>Boolean(tokens[0]&&tokens[1]))).filter((([tokenA,tokenB])=>tokenA.address!==tokenB.address&&!tokenA.equals(tokenB))).value(),poolAddressSet=new Set;return _(pairs).map((([tokenA,tokenB])=>{const poolAddress=Pair.getAddress(tokenA,tokenB);if(poolAddressSet.has(poolAddress))return;poolAddressSet.add(poolAddress);const[token0,token1]=tokenA.sortsBefore(tokenB)?[tokenA,tokenB]:[tokenB,tokenA];return{id:poolAddress,liquidity:"100",token0:{id:token0.address},token1:{id:token1.address},supply:100,reserve:100,reserveUSD:100}})).compact().value()}}