import{Protocol}from"@uniswap/router-sdk";import{Percent}from"@uniswap/sdk-core";import{Pair}from"@uniswap/v2-sdk";import{Pool}from"@uniswap/v3-sdk";import _ from"lodash";import{V3_CORE_FACTORY_ADDRESSES}from"./addresses";import{CurrencyAmount}from".";export const routeToString=route=>{const routeStr=[],tokens=route.protocol===Protocol.V3?route.tokenPath:route.path,tokenPath=_.map(tokens,(token=>`${token.symbol}`)),pools=route.protocol===Protocol.V3||route.protocol===Protocol.MIXED?route.pools:route.pairs,poolFeePath=_.map(pools,(pool=>(pool instanceof Pool?` -- ${pool.fee/1e4}% [${Pool.getAddress(pool.token0,pool.token1,pool.fee,void 0,V3_CORE_FACTORY_ADDRESSES[pool.chainId])}]`:` -- [${Pair.getAddress(pool.token0,pool.token1)}]`)+" --\x3e "));for(let i=0;i<tokenPath.length;i++)routeStr.push(tokenPath[i]),i<poolFeePath.length&&routeStr.push(poolFeePath[i]);return routeStr.join("")};export const routeAmountsToString=routeAmounts=>{const total=_.reduce(routeAmounts,((total,cur)=>total.add(cur.amount)),CurrencyAmount.fromRawAmount(routeAmounts[0].amount.currency,0)),routeStrings=_.map(routeAmounts,(({protocol:protocol,route:route,amount:amount})=>{const portion=amount.divide(total),percent=new Percent(portion.numerator,portion.denominator);return`[${protocol==Protocol.MIXED?"V2 + V3":protocol}] ${percent.toFixed(2)}% = ${routeToString(route)}`}));return _.join(routeStrings,", ")};export const routeAmountToString=routeAmount=>{const{route:route,amount:amount}=routeAmount;return`${amount.toExact()} = ${routeToString(route)}`};export const poolToString=p=>`${p.token0.symbol}/${p.token1.symbol}${p instanceof Pool?`/${p.fee/1e4}%`:""}`;