import{Protocol}from"@uniswap/router-sdk";import{TradeType}from"@uniswap/sdk-core";import{Pool}from"@uniswap/v3-sdk";import _ from"lodash";import{CurrencyAmount}from"../../../util/amounts";import{routeToString}from"../../../util/routes";export class V2RouteWithValidQuote{constructor({amount:amount,rawQuote:rawQuote,percent:percent,route:route,gasModel:gasModel,quoteToken:quoteToken,tradeType:tradeType,v2PoolProvider:v2PoolProvider}){this.protocol=Protocol.V2,this.amount=amount,this.rawQuote=rawQuote,this.quote=CurrencyAmount.fromRawAmount(quoteToken,rawQuote.toString()),this.percent=percent,this.route=route,this.gasModel=gasModel,this.quoteToken=quoteToken,this.tradeType=tradeType;const{gasEstimate:gasEstimate,gasCostInToken:gasCostInToken,gasCostInUSD:gasCostInUSD}=this.gasModel.estimateGasCost(this);if(this.gasCostInToken=gasCostInToken,this.gasCostInUSD=gasCostInUSD,this.gasEstimate=gasEstimate,this.tradeType==TradeType.EXACT_INPUT){const quoteGasAdjusted=this.quote.subtract(gasCostInToken);this.quoteAdjustedForGas=quoteGasAdjusted}else{const quoteGasAdjusted=this.quote.add(gasCostInToken);this.quoteAdjustedForGas=quoteGasAdjusted}this.poolAddresses=_.map(route.pairs,(p=>v2PoolProvider.getPoolAddress(p.token0,p.token1).poolAddress)),this.tokenPath=this.route.path}toString(){return`${this.percent.toFixed(2)}% QuoteGasAdj[${this.quoteAdjustedForGas.toExact()}] Quote[${this.quote.toExact()}] Gas[${this.gasEstimate.toString()}] = ${routeToString(this.route)}`}}export class V3RouteWithValidQuote{constructor({amount:amount,rawQuote:rawQuote,sqrtPriceX96AfterList:sqrtPriceX96AfterList,initializedTicksCrossedList:initializedTicksCrossedList,quoterGasEstimate:quoterGasEstimate,percent:percent,route:route,gasModel:gasModel,quoteToken:quoteToken,tradeType:tradeType,v3PoolProvider:v3PoolProvider}){this.protocol=Protocol.V3,this.amount=amount,this.rawQuote=rawQuote,this.sqrtPriceX96AfterList=sqrtPriceX96AfterList,this.initializedTicksCrossedList=initializedTicksCrossedList,this.quoterGasEstimate=quoterGasEstimate,this.quote=CurrencyAmount.fromRawAmount(quoteToken,rawQuote.toString()),this.percent=percent,this.route=route,this.gasModel=gasModel,this.quoteToken=quoteToken,this.tradeType=tradeType;const{gasEstimate:gasEstimate,gasCostInToken:gasCostInToken,gasCostInUSD:gasCostInUSD}=this.gasModel.estimateGasCost(this);if(this.gasCostInToken=gasCostInToken,this.gasCostInUSD=gasCostInUSD,this.gasEstimate=gasEstimate,this.tradeType==TradeType.EXACT_INPUT){const quoteGasAdjusted=this.quote.subtract(gasCostInToken);this.quoteAdjustedForGas=quoteGasAdjusted}else{const quoteGasAdjusted=this.quote.add(gasCostInToken);this.quoteAdjustedForGas=quoteGasAdjusted}this.poolAddresses=_.map(route.pools,(p=>v3PoolProvider.getPoolAddress(p.token0,p.token1,p.fee).poolAddress)),this.tokenPath=this.route.tokenPath}toString(){return`${this.percent.toFixed(2)}% QuoteGasAdj[${this.quoteAdjustedForGas.toExact()}] Quote[${this.quote.toExact()}] Gas[${this.gasEstimate.toString()}] = ${routeToString(this.route)}`}}export class MixedRouteWithValidQuote{constructor({amount:amount,rawQuote:rawQuote,sqrtPriceX96AfterList:sqrtPriceX96AfterList,initializedTicksCrossedList:initializedTicksCrossedList,quoterGasEstimate:quoterGasEstimate,percent:percent,route:route,mixedRouteGasModel:mixedRouteGasModel,quoteToken:quoteToken,tradeType:tradeType,v3PoolProvider:v3PoolProvider,v2PoolProvider:v2PoolProvider}){this.protocol=Protocol.MIXED,this.amount=amount,this.rawQuote=rawQuote,this.sqrtPriceX96AfterList=sqrtPriceX96AfterList,this.initializedTicksCrossedList=initializedTicksCrossedList,this.quoterGasEstimate=quoterGasEstimate,this.quote=CurrencyAmount.fromRawAmount(quoteToken,rawQuote.toString()),this.percent=percent,this.route=route,this.gasModel=mixedRouteGasModel,this.quoteToken=quoteToken,this.tradeType=tradeType;const{gasEstimate:gasEstimate,gasCostInToken:gasCostInToken,gasCostInUSD:gasCostInUSD}=this.gasModel.estimateGasCost(this);if(this.gasCostInToken=gasCostInToken,this.gasCostInUSD=gasCostInUSD,this.gasEstimate=gasEstimate,this.tradeType==TradeType.EXACT_INPUT){const quoteGasAdjusted=this.quote.subtract(gasCostInToken);this.quoteAdjustedForGas=quoteGasAdjusted}else{const quoteGasAdjusted=this.quote.add(gasCostInToken);this.quoteAdjustedForGas=quoteGasAdjusted}this.poolAddresses=_.map(route.pools,(p=>p instanceof Pool?v3PoolProvider.getPoolAddress(p.token0,p.token1,p.fee).poolAddress:v2PoolProvider.getPoolAddress(p.token0,p.token1).poolAddress)),this.tokenPath=this.route.path}toString(){return`${this.percent.toFixed(2)}% QuoteGasAdj[${this.quoteAdjustedForGas.toExact()}] Quote[${this.quote.toExact()}] Gas[${this.gasEstimate.toString()}] = ${routeToString(this.route)}`}}