import{ChainId}from"@uniswap/sdk-core";import{IGasPriceProvider}from"./gas-price-provider";const DEFAULT_EIP_1559_SUPPORTED_CHAINS=[ChainId.MAINNET,ChainId.GOERLI,ChainId.POLYGON_MUMBAI];export class OnChainGasPriceProvider extends IGasPriceProvider{constructor(chainId,eip1559GasPriceProvider,legacyGasPriceProvider,eipChains=DEFAULT_EIP_1559_SUPPORTED_CHAINS){super(),this.chainId=chainId,this.eip1559GasPriceProvider=eip1559GasPriceProvider,this.legacyGasPriceProvider=legacyGasPriceProvider,this.eipChains=eipChains}async getGasPrice(){return this.eipChains.includes(this.chainId)?this.eip1559GasPriceProvider.getGasPrice():this.legacyGasPriceProvider.getGasPrice()}}