import{Protocol}from"@uniswap/router-sdk";import{Pool}from"@uniswap/v3-sdk";export class CachedRoute{constructor({route:route,percent:percent}){this.hashCode=str=>[...str].reduce(((s,c)=>Math.imul(31,s)+c.charCodeAt(0)|0),0),this.route=route,this.percent=percent}get protocol(){return this.route.protocol}get tokenIn(){return this.route.input}get tokenOut(){return this.route.output}get routePath(){if(this.protocol==Protocol.V3){return this.route.pools.map((pool=>`[V3]${pool.token0.address}/${pool.token1.address}/${pool.fee}`)).join("->")}if(this.protocol==Protocol.V2){return this.route.pairs.map((pair=>`[V2]${pair.token0.address}/${pair.token1.address}`)).join("->")}return this.route.pools.map((pool=>pool instanceof Pool?`[V3]${pool.token0.address}/${pool.token1.address}/${pool.fee}`:`[V2]${pool.token0.address}/${pool.token1.address}`)).join("->")}get routeId(){return this.hashCode(this.routePath)}}