export class CachingV3SubgraphProvider{constructor(chainId,subgraphProvider,cache){this.chainId=chainId,this.subgraphProvider=subgraphProvider,this.cache=cache,this.SUBGRAPH_KEY=chainId=>`subgraph-pools-${chainId}`}async getPools(){const cachedPools=await this.cache.get(this.SUBGRAPH_KEY(this.chainId));if(cachedPools)return cachedPools;const pools=await this.subgraphProvider.getPools();return await this.cache.set(this.SUBGRAPH_KEY(this.chainId),pools),pools}}