import _ from"lodash";import{CachedRoute}from"./cached-route";export class CachedRoutes{constructor({routes:routes,chainId:chainId,tokenIn:tokenIn,tokenOut:tokenOut,protocolsCovered:protocolsCovered,blockNumber:blockNumber,tradeType:tradeType,originalAmount:originalAmount,blocksToLive:blocksToLive=0}){this.routes=routes,this.chainId=chainId,this.tokenIn=tokenIn,this.tokenOut=tokenOut,this.protocolsCovered=protocolsCovered,this.blockNumber=blockNumber,this.tradeType=tradeType,this.originalAmount=originalAmount,this.blocksToLive=blocksToLive}static fromRoutesWithValidQuotes(routes,chainId,tokenIn,tokenOut,protocolsCovered,blockNumber,tradeType,originalAmount){if(0==routes.length)return;const cachedRoutes=_.map(routes,(route=>new CachedRoute({route:route.route,percent:route.percent})));return new CachedRoutes({routes:cachedRoutes,chainId:chainId,tokenIn:tokenIn,tokenOut:tokenOut,protocolsCovered:protocolsCovered,blockNumber:blockNumber,tradeType:tradeType,originalAmount:originalAmount})}notExpired(currentBlockNumber,optimistic=!1){const blocksToLive=optimistic?this.blocksToLive:0;return currentBlockNumber-this.blockNumber<=blocksToLive}}