import _ from"lodash";import{log,metric,MetricLoggerUnit,poolToString}from"../../../util";export class BaseQuoter{constructor(tokenProvider,chainId,protocol,blockedTokenListProvider,tokenValidatorProvider){this.tokenProvider=tokenProvider,this.chainId=chainId,this.protocol=protocol,this.blockedTokenListProvider=blockedTokenListProvider,this.tokenValidatorProvider=tokenValidatorProvider}getRoutesThenQuotes(tokenIn,tokenOut,amount,amounts,percents,quoteToken,candidatePools,tradeType,routingConfig,gasModel,gasPriceWei){return this.getRoutes(tokenIn,tokenOut,candidatePools,tradeType,routingConfig).then((routesResult=>(1==routesResult.routes.length&&(metric.putMetric(`${this.protocol}QuoterSingleRoute`,1,MetricLoggerUnit.Count),percents=[100],amounts=[amount]),routesResult.routes.length>0?metric.putMetric(`${this.protocol}QuoterRoutesFound`,routesResult.routes.length,MetricLoggerUnit.Count):metric.putMetric(`${this.protocol}QuoterNoRoutesFound`,routesResult.routes.length,MetricLoggerUnit.Count),this.getQuotes(routesResult.routes,amounts,percents,quoteToken,tradeType,routingConfig,routesResult.candidatePools,gasModel,gasPriceWei))))}async applyTokenValidatorToPools(pools,isInvalidFn){if(!this.tokenValidatorProvider)return pools;log.info(`Running token validator on ${pools.length} pools`);const tokens=_.flatMap(pools,(pool=>[pool.token0,pool.token1])),tokenValidationResults=await this.tokenValidatorProvider.validateTokens(tokens);return _.filter(pools,(pool=>{const token0Validation=tokenValidationResults.getValidationByToken(pool.token0),token1Validation=tokenValidationResults.getValidationByToken(pool.token1),token0Invalid=isInvalidFn(pool.token0,token0Validation),token1Invalid=isInvalidFn(pool.token1,token1Validation);return(token0Invalid||token1Invalid)&&log.info(`Dropping pool ${poolToString(pool)} because token is invalid. ${pool.token0.symbol}: ${token0Validation}, ${pool.token1.symbol}: ${token1Validation}`),!token0Invalid&&!token1Invalid}))}}