import{BigNumber}from"@ethersproject/bignumber";import retry from"async-retry";import axios from"axios";import{log}from"../util/log";import{IGasPriceProvider}from"./gas-price-provider";export class ETHGasStationInfoProvider extends IGasPriceProvider{constructor(url){super(),this.url=url}async getGasPrice(){log.info(`About to get gas prices from gas station ${this.url}`);const response=await retry((async()=>axios.get(this.url)),{retries:1}),{data:gasPriceResponse,status:status}=response;if(200!=status)throw log.error({response:response},`Unabled to get gas price from ${this.url}.`),new Error(`Unable to get gas price from ${this.url}`);log.info({gasPriceResponse:gasPriceResponse},'Gas price response from API. About to parse "fast" to big number');const gasPriceWei=BigNumber.from(gasPriceResponse.fast).div(BigNumber.from(10)).mul(BigNumber.from(10).pow(9));return log.info(`Gas price in wei: ${gasPriceWei} as of block ${gasPriceResponse.blockNum}`),{gasPriceWei:gasPriceWei}}}