import{log}from"../util";import{IGasPriceProvider}from"./gas-price-provider";export class LegacyGasPriceProvider extends IGasPriceProvider{constructor(provider){super(),this.provider=provider}async getGasPrice(){const gasPriceWei=await this.provider.getGasPrice();return log.info({gasPriceWei:gasPriceWei},`Got gas price ${gasPriceWei} using eth_gasPrice RPC`),{gasPriceWei:gasPriceWei}}}