import{BigNumber}from"@ethersproject/bignumber";import{TradeType}from"@uniswap/sdk-core";import{InsufficientInputAmountError,InsufficientReservesError}from"@uniswap/v2-sdk";import{log}from"../../util/log";import{routeToString}from"../../util/routes";export class V2QuoteProvider{constructor(){}async getQuotesManyExactIn(amountIns,routes,providerConfig){return this.getQuotes(amountIns,routes,TradeType.EXACT_INPUT,providerConfig)}async getQuotesManyExactOut(amountOuts,routes,providerConfig){return this.getQuotes(amountOuts,routes,TradeType.EXACT_OUTPUT,providerConfig)}async getQuotes(amounts,routes,tradeType,providerConfig){const routesWithQuotes=[],debugStrs=[];for(const route of routes){const amountQuotes=[];let insufficientInputAmountErrorCount=0,insufficientReservesErrorCount=0;for(const amount of amounts)try{if(tradeType==TradeType.EXACT_INPUT){let outputAmount=amount.wrapped;for(const pair of route.pairs)[outputAmount]=pair.getOutputAmount(outputAmount,!0===providerConfig.enableFeeOnTransferFeeFetching);amountQuotes.push({amount:amount,quote:BigNumber.from(outputAmount.quotient.toString())})}else{let inputAmount=amount.wrapped;for(let i=route.pairs.length-1;i>=0;i--){const pair=route.pairs[i];[inputAmount]=pair.getInputAmount(inputAmount,!0===providerConfig.enableFeeOnTransferFeeFetching)}amountQuotes.push({amount:amount,quote:BigNumber.from(inputAmount.quotient.toString())})}}catch(err){if(err instanceof InsufficientInputAmountError)insufficientInputAmountErrorCount+=1,amountQuotes.push({amount:amount,quote:null});else{if(!(err instanceof InsufficientReservesError))throw err;insufficientReservesErrorCount+=1,amountQuotes.push({amount:amount,quote:null})}}(insufficientInputAmountErrorCount>0||insufficientReservesErrorCount>0)&&debugStrs.push(`${[routeToString(route)]} Input: ${insufficientInputAmountErrorCount} Reserves: ${insufficientReservesErrorCount} }`),routesWithQuotes.push([route,amountQuotes])}return debugStrs.length>0&&log.info({debugStrs:debugStrs},"Failed quotes for V2 routes"),{routesWithQuotes:routesWithQuotes}}}