import{log}from"../../util";export class V3SubgraphProviderWithFallBacks{constructor(fallbacks){this.fallbacks=fallbacks}async getPools(tokenIn,tokenOut,providerConfig){for(let i=0;i<this.fallbacks.length;i++){const provider=this.fallbacks[i];try{return await provider.getPools(tokenIn,tokenOut,providerConfig)}catch(err){log.info(`Failed to get subgraph pools for V3 from fallback #${i}`)}}throw new Error("Failed to get subgraph pools from any providers")}}