import{BigNumber}from"@ethersproject/bignumber";import _ from"lodash";import{log}from"../util/log";import{IGasPriceProvider}from"./gas-price-provider";const DEFAULT_PRIORITY_FEE_PERCENTILE=50,DEFAULT_BLOCKS_TO_LOOK_BACK=4;export class EIP1559GasPriceProvider extends IGasPriceProvider{constructor(provider,priorityFeePercentile=50,blocksToConsider=4){super(),this.provider=provider,this.priorityFeePercentile=priorityFeePercentile,this.blocksToConsider=blocksToConsider}async getGasPrice(){const feeHistoryRaw=await this.provider.send("eth_feeHistory",[BigNumber.from(this.blocksToConsider).toHexString().replace("0x0","0x"),"latest",[this.priorityFeePercentile]]),feeHistory={baseFeePerGas:_.map(feeHistoryRaw.baseFeePerGas,(b=>BigNumber.from(b))),gasUsedRatio:feeHistoryRaw.gasUsedRatio,oldestBlock:BigNumber.from(feeHistoryRaw.oldestBlock),reward:_.map(feeHistoryRaw.reward,(b=>BigNumber.from(b[0])))},nextBlockBaseFeePerGas=feeHistory.baseFeePerGas[feeHistory.baseFeePerGas.length-1],averagePriorityFeePerGas=_.reduce(feeHistory.reward,((sum,cur)=>sum.add(cur)),BigNumber.from(0)).div(feeHistory.reward.length);log.info({feeHistory:feeHistory,feeHistoryReadable:{baseFeePerGas:_.map(feeHistory.baseFeePerGas,(f=>f.toString())),oldestBlock:feeHistory.oldestBlock.toString(),reward:_.map(feeHistory.reward,(r=>r.toString()))},nextBlockBaseFeePerGas:nextBlockBaseFeePerGas.toString(),averagePriorityFeePerGas:averagePriorityFeePerGas.toString()},"Got fee history from provider and computed gas estimate");const gasPriceWei=nextBlockBaseFeePerGas.add(averagePriorityFeePerGas),blockNumber=feeHistory.oldestBlock.add(this.blocksToConsider);return log.info(`Estimated gas price in wei: ${gasPriceWei} as of block ${blockNumber.toString()}`),{gasPriceWei:gasPriceWei}}}