import{BigNumber}from"@ethersproject/bignumber";import{ZERO}from"@uniswap/router-sdk";import{Fraction,TradeType}from"@uniswap/sdk-core";import{SwapType}from"../routers";import{CurrencyAmount}from"../util";export class PortionProvider{getPortionAmount(tokenOutAmount,tradeType,swapConfig){if((null==swapConfig?void 0:swapConfig.type)!==SwapType.UNIVERSAL_ROUTER)return;const swapConfigUniversalRouter=swapConfig;switch(tradeType){case TradeType.EXACT_INPUT:return swapConfigUniversalRouter.fee&&swapConfigUniversalRouter.fee.fee.greaterThan(ZERO)?tokenOutAmount.multiply(swapConfigUniversalRouter.fee.fee):void 0;case TradeType.EXACT_OUTPUT:return swapConfigUniversalRouter.flatFee&&swapConfigUniversalRouter.flatFee.amount>BigNumber.from(0)?CurrencyAmount.fromRawAmount(tokenOutAmount.currency,swapConfigUniversalRouter.flatFee.amount.toString()):void 0;default:throw new Error(`Unknown trade type ${tradeType}`)}}getPortionQuoteAmount(tradeType,quote,portionAdjustedAmount,portionAmount){if(!portionAmount)return;if(tradeType!==TradeType.EXACT_OUTPUT)return;const portionToPortionAdjustedAmountRatio=new Fraction(portionAmount.quotient,portionAdjustedAmount.quotient);return CurrencyAmount.fromRawAmount(quote.currency,portionToPortionAdjustedAmountRatio.multiply(quote).quotient)}getRouteWithQuotePortionAdjusted(tradeType,routeWithQuotes,swapConfig){return tradeType!==TradeType.EXACT_INPUT||(null==swapConfig?void 0:swapConfig.type)!==SwapType.UNIVERSAL_ROUTER?routeWithQuotes:routeWithQuotes.map((routeWithQuote=>{const portionAmount=this.getPortionAmount(routeWithQuote.quote,tradeType,swapConfig);return portionAmount&&(routeWithQuote.quote=routeWithQuote.quote.subtract(portionAmount)),routeWithQuote}))}getQuote(tradeType,quote,portionQuoteAmount){switch(tradeType){case TradeType.EXACT_INPUT:return quote;case TradeType.EXACT_OUTPUT:return portionQuoteAmount?quote.subtract(portionQuoteAmount):quote;default:throw new Error(`Unknown trade type ${tradeType}`)}}getQuoteGasAdjusted(tradeType,quoteGasAdjusted,portionQuoteAmount){switch(tradeType){case TradeType.EXACT_INPUT:return quoteGasAdjusted;case TradeType.EXACT_OUTPUT:return portionQuoteAmount?quoteGasAdjusted.subtract(portionQuoteAmount):quoteGasAdjusted;default:throw new Error(`Unknown trade type ${tradeType}`)}}getQuoteGasAndPortionAdjusted(tradeType,quoteGasAdjusted,portionAmount){if(portionAmount)switch(tradeType){case TradeType.EXACT_INPUT:return quoteGasAdjusted.subtract(portionAmount);case TradeType.EXACT_OUTPUT:return quoteGasAdjusted;default:throw new Error(`Unknown trade type ${tradeType}`)}}}