import retry from"async-retry";import Timeout from"await-timeout";import axios from"axios";import{log}from"../util/log";export class URISubgraphProvider{constructor(chainId,uri,timeout=6e3,retries=2){this.chainId=chainId,this.uri=uri,this.timeout=timeout,this.retries=retries}async getPools(){log.info({uri:this.uri},`About to get subgraph pools from URI ${this.uri}`);let allPools=[];return await retry((async()=>{const timeout=new Timeout,timerPromise=timeout.set(this.timeout).then((()=>{throw new Error(`Timed out getting pools from subgraph: ${this.timeout}`)}));let response;try{response=await Promise.race([axios.get(this.uri),timerPromise])}catch(err){throw err}finally{timeout.clear()}const{data:poolsBuffer,status:status}=response;if(200!=status)throw log.error({response:response},`Unabled to get pools from ${this.uri}.`),new Error(`Unable to get pools from ${this.uri}`);const pools=poolsBuffer;log.info({uri:this.uri,chain:this.chainId},`Got subgraph pools from uri. Num: ${pools.length}`),allPools=pools}),{retries:this.retries,onRetry:(err,retry)=>{log.info({err:err},`Failed to get pools from uri ${this.uri}. Retry attempt: ${retry}`)}}),allPools}}