import{Protocol}from"@uniswap/router-sdk";import{TradeType}from"@uniswap/sdk-core";import _ from"lodash";import{TokenValidationResult}from"../../../providers";import{log,metric,MetricLoggerUnit,routeToString}from"../../../util";import{V3RouteWithValidQuote}from"../entities";import{computeAllV3Routes}from"../functions/compute-all-routes";import{BaseQuoter}from"./base-quoter";export class V3Quoter extends BaseQuoter{constructor(v3SubgraphProvider,v3PoolProvider,onChainQuoteProvider,tokenProvider,chainId,blockedTokenListProvider,tokenValidatorProvider){super(tokenProvider,chainId,Protocol.V3,blockedTokenListProvider,tokenValidatorProvider),this.v3SubgraphProvider=v3SubgraphProvider,this.v3PoolProvider=v3PoolProvider,this.onChainQuoteProvider=onChainQuoteProvider}async getRoutes(tokenIn,tokenOut,v3CandidatePools,_tradeType,routingConfig){const beforeGetRoutes=Date.now(),{poolAccessor:poolAccessor,candidatePools:candidatePools}=v3CandidatePools,poolsRaw=poolAccessor.getAllPools(),pools=await this.applyTokenValidatorToPools(poolsRaw,((token,tokenValidation)=>!!tokenValidation&&((tokenValidation!=TokenValidationResult.STF||!token.equals(tokenIn)&&!token.equals(tokenOut))&&(tokenValidation==TokenValidationResult.FOT||tokenValidation==TokenValidationResult.STF)))),{maxSwapsPerPath:maxSwapsPerPath}=routingConfig,routes=computeAllV3Routes(tokenIn,tokenOut,pools,maxSwapsPerPath);return metric.putMetric("V3GetRoutesLoad",Date.now()-beforeGetRoutes,MetricLoggerUnit.Milliseconds),{routes:routes,candidatePools:candidatePools}}async getQuotes(routes,amounts,percents,quoteToken,tradeType,routingConfig,candidatePools,gasModel){const beforeGetQuotes=Date.now();if(log.info("Starting to get V3 quotes"),void 0===gasModel)throw new Error("GasModel for V3RouteWithValidQuote is required to getQuotes");if(0==routes.length)return{routesWithValidQuotes:[],candidatePools:candidatePools};const quoteFn=tradeType==TradeType.EXACT_INPUT?this.onChainQuoteProvider.getQuotesManyExactIn.bind(this.onChainQuoteProvider):this.onChainQuoteProvider.getQuotesManyExactOut.bind(this.onChainQuoteProvider),beforeQuotes=Date.now();log.info(`Getting quotes for V3 for ${routes.length} routes with ${amounts.length} amounts per route.`);const{routesWithQuotes:routesWithQuotes}=await quoteFn(amounts,routes,{blockNumber:routingConfig.blockNumber});metric.putMetric("V3QuotesLoad",Date.now()-beforeQuotes,MetricLoggerUnit.Milliseconds),metric.putMetric("V3QuotesFetched",_(routesWithQuotes).map((([,quotes])=>quotes.length)).sum(),MetricLoggerUnit.Count);const routesWithValidQuotes=[];for(const routeWithQuote of routesWithQuotes){const[route,quotes]=routeWithQuote;for(let i=0;i<quotes.length;i++){const percent=percents[i],amountQuote=quotes[i],{quote:quote,amount:amount,sqrtPriceX96AfterList:sqrtPriceX96AfterList,initializedTicksCrossedList:initializedTicksCrossedList,gasEstimate:gasEstimate}=amountQuote;if(!(quote&&sqrtPriceX96AfterList&&initializedTicksCrossedList&&gasEstimate)){log.debug({route:routeToString(route),amountQuote:amountQuote},"Dropping a null V3 quote for route.");continue}const routeWithValidQuote=new V3RouteWithValidQuote({route:route,rawQuote:quote,amount:amount,percent:percent,sqrtPriceX96AfterList:sqrtPriceX96AfterList,initializedTicksCrossedList:initializedTicksCrossedList,quoterGasEstimate:gasEstimate,gasModel:gasModel,quoteToken:quoteToken,tradeType:tradeType,v3PoolProvider:this.v3PoolProvider});routesWithValidQuotes.push(routeWithValidQuote)}}return metric.putMetric("V3GetQuotesLoad",Date.now()-beforeGetQuotes,MetricLoggerUnit.Milliseconds),{routesWithValidQuotes:routesWithValidQuotes,candidatePools:candidatePools}}}