import{Fraction}from"@uniswap/sdk-core";import{CurrencyAmount}from"../../../util/amounts";export function calculateRatioAmountIn(optimalRatio,inputTokenPrice,inputBalance,outputBalance){const amountToSwapRaw=new Fraction(inputBalance.quotient).subtract(optimalRatio.multiply(outputBalance.quotient)).divide(optimalRatio.multiply(inputTokenPrice).add(1));if(amountToSwapRaw.lessThan(0))throw new Error("routeToRatio: insufficient input token amount");return CurrencyAmount.fromRawAmount(inputBalance.currency,amountToSwapRaw.quotient)}