import{computePoolAddress,Pool}from"@uniswap/v3-sdk";import retry from"async-retry";import _ from"lodash";import{IUniswapV3PoolState__factory}from"../../types/v3/factories/IUniswapV3PoolState__factory";import{V3_CORE_FACTORY_ADDRESSES}from"../../util/addresses";import{log}from"../../util/log";import{poolToString}from"../../util/routes";export class V3PoolProvider{constructor(chainId,multicall2Provider,retryOptions={retries:2,minTimeout:50,maxTimeout:500}){this.chainId=chainId,this.multicall2Provider=multicall2Provider,this.retryOptions=retryOptions,this.POOL_ADDRESS_CACHE={}}async getPools(tokenPairs,providerConfig){const poolAddressSet=new Set,sortedTokenPairs=[],sortedPoolAddresses=[];for(const tokenPair of tokenPairs){const[tokenA,tokenB,feeAmount]=tokenPair,{poolAddress:poolAddress,token0:token0,token1:token1}=this.getPoolAddress(tokenA,tokenB,feeAmount);poolAddressSet.has(poolAddress)||(poolAddressSet.add(poolAddress),sortedTokenPairs.push([token0,token1,feeAmount]),sortedPoolAddresses.push(poolAddress))}log.debug(`getPools called with ${tokenPairs.length} token pairs. Deduped down to ${poolAddressSet.size}`);const[slot0Results,liquidityResults]=await Promise.all([this.getPoolsData(sortedPoolAddresses,"slot0",providerConfig),this.getPoolsData(sortedPoolAddresses,"liquidity",providerConfig)]);log.info(`Got liquidity and slot0s for ${poolAddressSet.size} pools ${(null==providerConfig?void 0:providerConfig.blockNumber)?`as of block: ${null==providerConfig?void 0:providerConfig.blockNumber}.`:""}`);const poolAddressToPool={},invalidPools=[];for(let i=0;i<sortedPoolAddresses.length;i++){const slot0Result=slot0Results[i],liquidityResult=liquidityResults[i];if(!(null==slot0Result?void 0:slot0Result.success)||!(null==liquidityResult?void 0:liquidityResult.success)||slot0Result.result.sqrtPriceX96.eq(0)){const[token0,token1,fee]=sortedTokenPairs[i];invalidPools.push([token0,token1,fee]);continue}const[token0,token1,fee]=sortedTokenPairs[i],slot0=slot0Result.result,liquidity=liquidityResult.result[0],pool=new Pool(token0,token1,fee,slot0.sqrtPriceX96.toString(),liquidity.toString(),slot0.tick),poolAddress=sortedPoolAddresses[i];poolAddressToPool[poolAddress]=pool}invalidPools.length>0&&log.info({invalidPools:_.map(invalidPools,(([token0,token1,fee])=>`${token0.symbol}/${token1.symbol}/${fee/1e4}%`))},`${invalidPools.length} pools invalid after checking their slot0 and liquidity results. Dropping.`);const poolStrs=_.map(Object.values(poolAddressToPool),poolToString);return log.debug({poolStrs:poolStrs},`Found ${poolStrs.length} valid pools`),{getPool:(tokenA,tokenB,feeAmount)=>{const{poolAddress:poolAddress}=this.getPoolAddress(tokenA,tokenB,feeAmount);return poolAddressToPool[poolAddress]},getPoolByAddress:address=>poolAddressToPool[address],getAllPools:()=>Object.values(poolAddressToPool)}}getPoolAddress(tokenA,tokenB,feeAmount){const[token0,token1]=tokenA.sortsBefore(tokenB)?[tokenA,tokenB]:[tokenB,tokenA],cacheKey=`${this.chainId}/${token0.address}/${token1.address}/${feeAmount}`,cachedAddress=this.POOL_ADDRESS_CACHE[cacheKey];if(cachedAddress)return{poolAddress:cachedAddress,token0:token0,token1:token1};const poolAddress=computePoolAddress({factoryAddress:V3_CORE_FACTORY_ADDRESSES[this.chainId],tokenA:token0,tokenB:token1,fee:feeAmount});return this.POOL_ADDRESS_CACHE[cacheKey]=poolAddress,{poolAddress:poolAddress,token0:token0,token1:token1}}async getPoolsData(poolAddresses,functionName,providerConfig){const{results:results,blockNumber:blockNumber}=await retry((async()=>this.multicall2Provider.callSameFunctionOnMultipleContracts({addresses:poolAddresses,contractInterface:IUniswapV3PoolState__factory.createInterface(),functionName:functionName,providerConfig:providerConfig})),this.retryOptions);return log.debug(`Pool data fetched as of block ${blockNumber}`),results}}