import{log}from"../util/log";export class CachingGasStationProvider{constructor(chainId,gasPriceProvider,cache){this.chainId=chainId,this.gasPriceProvider=gasPriceProvider,this.cache=cache,this.GAS_KEY=chainId=>`gasPrice-${chainId}`}async getGasPrice(){const cachedGasPrice=await this.cache.get(this.GAS_KEY(this.chainId));if(cachedGasPrice)return log.info({cachedGasPrice:cachedGasPrice},`Got gas station price from local cache: ${cachedGasPrice.gasPriceWei}.`),cachedGasPrice;log.info("Gas station price local cache miss.");const gasPrice=await this.gasPriceProvider.getGasPrice();return await this.cache.set(this.GAS_KEY(this.chainId),gasPrice),gasPrice}}