import{BigNumber}from"@ethersproject/bignumber";import{ChainId}from"@uniswap/sdk-core";import{AAVE_MAINNET,LIDO_MAINNET}from"../../../../providers";export const COST_PER_UNINIT_TICK=BigNumber.from(0);export const BASE_SWAP_COST=id=>{switch(id){case ChainId.MAINNET:case ChainId.GOERLI:case ChainId.SEPOLIA:case ChainId.OPTIMISM:case ChainId.OPTIMISM_GOERLI:case ChainId.BNB:case ChainId.AVALANCHE:case ChainId.BASE:case ChainId.BASE_GOERLI:return BigNumber.from(2e3);case ChainId.ARBITRUM_ONE:case ChainId.ARBITRUM_GOERLI:return BigNumber.from(5e3);case ChainId.POLYGON:case ChainId.POLYGON_MUMBAI:case ChainId.CELO:case ChainId.CELO_ALFAJORES:case ChainId.GNOSIS:case ChainId.MOONBEAM:return BigNumber.from(2e3)}};export const COST_PER_INIT_TICK=id=>{switch(id){case ChainId.MAINNET:case ChainId.GOERLI:case ChainId.SEPOLIA:case ChainId.BNB:case ChainId.AVALANCHE:case ChainId.OPTIMISM:case ChainId.OPTIMISM_GOERLI:case ChainId.BASE:case ChainId.BASE_GOERLI:case ChainId.ARBITRUM_ONE:case ChainId.ARBITRUM_GOERLI:case ChainId.POLYGON:case ChainId.POLYGON_MUMBAI:case ChainId.CELO:case ChainId.CELO_ALFAJORES:case ChainId.GNOSIS:case ChainId.MOONBEAM:return BigNumber.from(31e3)}};export const COST_PER_HOP=id=>{switch(id){case ChainId.MAINNET:case ChainId.GOERLI:case ChainId.SEPOLIA:case ChainId.BNB:case ChainId.OPTIMISM:case ChainId.OPTIMISM_GOERLI:case ChainId.AVALANCHE:case ChainId.BASE:case ChainId.BASE_GOERLI:case ChainId.ARBITRUM_ONE:case ChainId.ARBITRUM_GOERLI:case ChainId.POLYGON:case ChainId.POLYGON_MUMBAI:case ChainId.CELO:case ChainId.CELO_ALFAJORES:case ChainId.GNOSIS:case ChainId.MOONBEAM:return BigNumber.from(8e4)}};export const SINGLE_HOP_OVERHEAD=_id=>BigNumber.from(15e3);export const TOKEN_OVERHEAD=(id,route)=>{const tokens=route.tokenPath;let overhead=BigNumber.from(0);return id==ChainId.MAINNET&&(tokens.some((t=>t.equals(AAVE_MAINNET)))&&(overhead=overhead.add(15e4)),tokens.some((t=>t.equals(LIDO_MAINNET)))&&(overhead=overhead.add(15e4))),overhead};export const NATIVE_WRAP_OVERHEAD=id=>BigNumber.from(27938);export const NATIVE_UNWRAP_OVERHEAD=id=>BigNumber.from(36e3);export const NATIVE_OVERHEAD=(chainId,amount,quote)=>amount.isNative?NATIVE_WRAP_OVERHEAD():quote.isNative?NATIVE_UNWRAP_OVERHEAD():BigNumber.from(0);