export class NodeJSCache{constructor(nodeCache){this.nodeCache=nodeCache}async get(key){return this.nodeCache.get(key)}async batchGet(keys){const keysArr=Array.from(keys),values=await Promise.all(keysArr.map((key=>this.get(key)))),result={};return keysArr.forEach(((key,index)=>{result[key]=values[index]})),result}async set(key,value,ttl){return ttl?this.nodeCache.set(key,value,ttl):this.nodeCache.set(key,value)}async has(key){return this.nodeCache.has(key)}}